import checkIcon from "../../../assets/icons/Vector.svg";
import { useCheckbox, chakra, Flex, Image, UseCheckboxProps } from "@chakra-ui/react";

export const CustomCheckbox = (props?: UseCheckboxProps) => {
    const { state, getCheckboxProps, getInputProps, htmlProps } = useCheckbox(props);

    return (
        <chakra.label
            display="flex"
            flexDirection="row"
            alignItems="center"
            // gridColumnGap={2}
            w="25px"
            bg="dark"
            borderRadius="2px"
            // px={1.4}
            h={6}
            cursor="pointer"
            {...htmlProps}
        >
            <input {...getInputProps()} hidden />
            <Flex
                alignItems="center"
                justifyContent="center"
                // border='2px solid'
                // borderColor='green.500'
                px={2}
                h={6}
                {...getCheckboxProps()}
            >
                {state.isChecked && <Image src={checkIcon} />}
            </Flex>
        </chakra.label>
    );
};