// @flow
import {
  Flex,
  Text,
  Spacer,
  Image,
  Stack,
  Box,
  Spinner,
  Center,
} from "@chakra-ui/react";
import { lazy, Suspense, useEffect, useState } from "react";
// import DashboardNavBar from "../../components/DashboardNavBar";
import lineicon from "../../assets/icons/Line 69.svg";
import { NotificationType, useGetAllNotificationsQuery } from "../../redux/rtkQuery/api";
import Container from "../../components/Container";
import Lottie from "lottie-react";
import noNotificationAnimation from "../../assets/json/4021-no-notification-state.json";
import { useDispatch, useSelector } from "react-redux";
import { AuthReducerProps, UserReducerProps } from "../../interfaces";
import { RootState } from "../../redux/store";
import { setPrevNotificationCount } from "../../redux/UserSlice";
import { Dispatch } from "redux";
import { nanoid } from "@reduxjs/toolkit";
import { FilterListItem } from "./components/FilterListItem";
import { filterItems } from "./interface";
import { NotificationItem } from "./components/NotificationItem";
// const DashboardNavBar = lazy(() => import("../../components/DashboardNavBar"));
const NewDashboardNavBar = lazy(() => import("../../components/NewDashboardNavBar"));

const style = {
  height: 300,
};

export const Notification = () => {
  const [isFilterActive, setIsFilterActive] = useState<boolean>(true);
  const [filter, setFilter] = useState<NotificationType | undefined>(undefined);
  const [selectedNotification, setSelectedNotification] = useState<string | undefined>(undefined);

  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);
  const { prev_notification_count }: UserReducerProps = useSelector(
    (state: RootState) => state.userReducer
  );
  const dispatch = useDispatch<Dispatch>();

  const {
    root,
    nameStyles,
    boxCircleColor,
    leftSection,
    rightSection,
    lowerSec,
    headingStyle,
    subHeadStyle,
    bodyStyle,
    headingStyle2,
  } = useNotificationStyles();

  const { data, isLoading } = useGetAllNotificationsQuery(
    { page: 1, type: filter },
    {
      refetchOnMountOrArgChange: true,
      skip: !token,
    }
  );


  const notificationArray: any = data?.data;

  // console.log("notificationArray", data?.data);
  // console.log('filter', filter)

  useEffect(() => {
    dispatch(setPrevNotificationCount(notificationArray?.total));
  }, [notificationArray?.total]);

  return (
    <Flex flexDir={"column"}>
      {/* ----------------------------------------------------------------------NAV BAR------------------------------------------------------------ */}
      <Suspense fallback={<Spinner size="sm" />}>
        {/* <DashboardNavBar /> */}
        <NewDashboardNavBar />
      </Suspense>
      {/* ----------------------------------------------------------------------TOP CARDS------------------------------------------------------------ */}

      <Container>
        <Stack {...root}>
          <Text {...nameStyles}>
            Notifications ({notificationArray?.total})
            {/* {notificationArray?.total - prev_notification_count > 0 &&
              `(
            ${notificationArray?.total - prev_notification_count})`} */}
          </Text>
          <Spacer />
          <Flex>
            {/* -----------------------------------LEFT SECTION---------------------------------------------- */}
            {
              isLoading && <Center><Spinner alignContent={'center'} justifyContent={'center'} /></Center>
            }
            {
              notificationArray && !isLoading && (
                <Flex {...leftSection} flexDir={"column"}>
                  {notificationArray && notificationArray.total === 0 && (
                    <Box key={nanoid()}>
                      <Lottie animationData={noNotificationAnimation} style={style} />
                      <Text>Dear user, you have no notification yet.</Text>
                    </Box>
                  )}
                  <Flex flexDir="column">
                    {notificationArray && notificationArray.notifications?.map((e: any) => (
                      <NotificationItem key={nanoid()} dto={e} lowerSec={{ ...lowerSec }} bodyStyle={{ ...bodyStyle }} boxCircleColor={{ ...boxCircleColor }} subHeadStyle={{ ...subHeadStyle }} />
                    ))}
                  </Flex>
                </Flex>
              )
            }

            <Spacer m={"1%"} />
            {/* -----------------------------------RIGHT SECTION---------------------------------------------- */}
            <Flex {...rightSection} flexDir="column">
              <Flex flexDir="column">
                {/* +++++++++++++++++++++++++++++++ Filters ++++++++++++++++++++++++++++++++++++++ */}
                <Flex
                  flexDir={"row"}
                  justify={"space-between"}
                  padding="1rem"
                  border={"1px solid rgba(0, 0, 0, 0.07)"}
                >
                  <Text {...headingStyle}>Filters</Text>
                  <Flex>
                    {/* <Checkbox borderColor={"#00000027"} spacing={"0.5rem"}></Checkbox> */}
                    <Box
                      as="button"
                      borderRadius="2px"
                      bg="#000000"
                      color="white"
                      px={2}
                      h={6}
                      onClick={() => setIsFilterActive(!isFilterActive)}
                    >
                      <Image src={lineicon} />
                    </Box>
                  </Flex>
                </Flex>
                {/* -------------------------------- Filter Contents ---------------------------- */}
                {isFilterActive && (
                  <Flex flexDir={"column"}>
                    {/* map the notification type as an array instead of listing them separately  */}
                    {
                      filterItems.map((item, index) => {
                        // if index is even set borderColor else set bg to F2F2F2 instead.
                        return (
                          <FilterListItem
                            key={nanoid()}
                            isItemEven={index % 2 ? false : true}
                            type={item.type}
                            itemHeading={item.header}
                            itemSubHeading={item.subHeader}
                            itemHeadingProps={{ ...headingStyle }}
                            itemSubHeadingProps={{ ...headingStyle2 }}
                            selectedFilter={filter}
                            setFilter={setFilter}
                          />
                        )
                      })
                    }

                  </Flex>
                )}
              </Flex>
            </Flex>
          </Flex>
        </Stack>
      </Container>
    </Flex>
  );
};

export default Notification;

